import React, { useRef, useState, useEffect } from 'react';
import ReactPlayer from 'react-player';
import { Range, getTrackBackground } from 'react-range';

import DummyVideo from '../../../Common/Videos/DummyVideo.mp4';

const DEFAULT_MAX = 100;

export default function GetVideoInput() {
  const [video, setVideo] = useState(null);
  const [trimStart, setTrimStart] = useState(0);
  const [trimEnd, setTrimEnd] = useState(DEFAULT_MAX);
  const [uploadDisabled, setUploadDisabled] = useState(false);
  const [duration, setDuration] = useState(0);
  const [currentTime, setCurrentTime] = useState(0);
  const [seeking, setSeeking] = useState(false);
  const videoRef = useRef(null);
  const inputRef = useRef(null);

  const handleVideoClick = () => {
    if (!uploadDisabled) {
      inputRef.current.click();
    }
  };

  const handleVideoChange = (e) => {
    const file = e.target.files[0];
    setVideo(file);
    setTrimStart(0);
    setTrimEnd(DEFAULT_MAX);
    setUploadDisabled(true);
  };

  const handleTrim = ({ playedSeconds }) => {
    if (!seeking) {
      const start = (trimStart / DEFAULT_MAX) * duration;
      const end = (trimEnd / DEFAULT_MAX) * duration;

      // Ensure playedSeconds stays within the trimmed range
      if (playedSeconds < start) {
        videoRef.current.seekTo(end, 'seconds', 'exact');
      } else if (playedSeconds > end) {
        videoRef.current.seekTo(start, 'seconds', 'exact');
      } else {
        setCurrentTime(playedSeconds - start);
      }
    }
  };

  const handleTrimChange = (values) => {
    setTrimStart(values[0]);
    setTrimEnd(values[1]);
  };

  const handleStartTrim = () => {
    setTrimStart((videoRef.current.getCurrentTime() / duration) * DEFAULT_MAX);
  };

  const handleSeek = () => {
    setSeeking(true);
  };

  const handleSeekChange = (seekTime) => {
    const start = (trimStart / DEFAULT_MAX) * duration;
    const end = (trimEnd / DEFAULT_MAX) * duration;
    const newTime = start + seekTime;

    // Ensure the new time stays within the trimmed range
    if (newTime < start) {
      videoRef.current.seekTo(end, 'seconds', 'exact');
    } else if (newTime > end) {
      videoRef.current.seekTo(start, 'seconds', 'exact');
    } else {
      videoRef.current.seekTo(newTime, 'seconds', 'exact');
    }

    setSeeking(false);
  };

  const handleReload = () => {
    setVideo(null);
    setUploadDisabled(false);
  };

  useEffect(() => {
    setCurrentTime(0);
  }, [video]);

  return (
    <div className='' onClick={handleVideoClick}>
      <div className="card">
        {video ? (
          <React.Fragment>
            {video instanceof Blob && (
              <ReactPlayer
                ref={videoRef}
                className="img-fluid"
                url={URL.createObjectURL(video)}
                controls
                playing
                onProgress={handleTrim}
                onDuration={(duration) => setDuration(duration)}
                onSeek={handleSeek}
              />
            )}

            <div className="d-flex m-1 g-0 d-flex justify-content-evenly">
              <button
                className='btn btn-sm btn-success text-light fw-bold btn-sm'
                onClick={handleStartTrim}
              >
                Start Trim
              </button>
              <button
                className='btn btn-sm btn-dark text-light fw-bold btn-sm'
                onClick={handleReload}
              >
                Reload for New Upload
              </button>
            </div>

            <Range
              min={0}
              max={DEFAULT_MAX}
              values={[trimStart, trimEnd]}
              onChange={(values) => handleTrimChange(values)}
              onFinalChange={(values) => handleSeekChange(values[0])}
              renderTrack={({ props, children }) => (
                <div
                  {...props}
                  style={{
                    ...props.style,
                    height: '6px',
                    width: '100%',
                    background: getTrackBackground({
                      values: [trimStart, trimEnd],
                      colors: ['#ccc', '#548BF4', '#ccc'],
                      min: 0,
                      max: DEFAULT_MAX,
                    }),
                  }}
                >
                  {children}
                </div>
              )}
              renderThumb={({ props }) => (
                <div
                  {...props}
                  style={{
                    ...props.style,
                    height: '20px',
                    width: '20px',
                    backgroundColor: '#FFF',
                    boxShadow: '0 2px 6px rgba(0, 0, 0, 0.1)',
                    borderRadius: '50%',
                    outline: 'none',
                  }}
                />
              )}
            />
            
            <div className="text-center bg-dark text-light mt-2">
              Selected Time: {currentTime.toFixed(2)} seconds
            </div>
          </React.Fragment>
        ) : (
          <video
            src={DummyVideo}
            className="object-fit-cover object-fit-fill"
            autoPlay
            loop
            muted
          ></video>
        )}

        <div className="card-body bg-dark">
          <input
            className="form-control d-none"
            onChange={handleVideoChange}
            ref={inputRef}
            type="file"
          />

          <h5 className="fa-fade text-light fw-bold text-center">
            {uploadDisabled
              ? 'Video Uploaded. Click Reload button to upload a new file'
              : 'Click To Upload Your File'}
          </h5>
        </div>
      </div>
    </div>
  );
}
